.contacts {
  &__section {
    margin-bottom: 24px;
  }

  &__title {
    margin: 0;
    margin-bottom: 8px;

    font-size: 14px;
    font-weight: 400;
    line-height: 140%; /* 19.6px */
    text-transform: capitalize;
  }

  &__info {
    margin: 0;

    display: block;
    font-size: 16px;
    font-style: normal;
    font-weight: 500;
    line-height: 140%; /* 22.4px */
    color: $main-text-color;
    text-decoration: none;

    transform-origin: left top;
    @include hover(transform, scale(1.2));
  }
}
