.about {
  @include page-grid;

  &__title {
    margin-bottom: 32px;

    grid-column: 1 / -1;

    @include onTablet {
      margin-bottom: 0;

      grid-column: 1 / 4;
    }

    @include onDesktop {
      grid-column: span 5;
    }
  }

  &__content {
    grid-column: 1 / -1;

    @include onTablet {
      grid-column: 4 / 7;
    }

    @include onDesktop {
      grid-column: span 6;
    }
  }

  &__description {
    font-size: 23px;
    font-weight: 500;
    line-height: 140%; // 32.2px
    margin: 0;
  }
}
